<template><section>
<h2 id="eldatatable"><a class="header-anchor" href="#eldatatable" aria-hidden="true">¶</a> ElDataTable</h2>
<p>基于 Vue2.x, element-ui 2.x，以及开源组件 <a href="https://github.com/leezng/el-form-renderer">el-form-renderer</a> 封装了一个业务组件 <a href="https://github.com/FEMessage/el-data-table">el-data-table</a>，已在<a href="https://github.com/FEMessage/el-data-table">github 开源</a>，其目标是：</p>
<div class="tip">
<p>makes restful api crud easily</p>
</div>
<h3 id="mo-ren-pei-zhi"><a class="header-anchor" href="#mo-ren-pei-zhi" aria-hidden="true">¶</a> 默认配置</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;el-data-table url=\&quot;https://cnodejs.org/api/v1/topics\&quot; data-path=\&quot;data\&quot; :form-attrs=\&quot;{ labelWidth: '80px' }\&quot; :custom-query=\&quot;{ limit: 5 }\&quot; :pagination-sizes=\&quot;[5,10,15]\&quot; :pagination-size=\&quot;5\&quot; :columns=\&quot;columns\&quot; :form=\&quot;form\&quot;&gt;\n      &lt;/el-data-table&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nimport ElDataTable from 'el-data-table'\n\nexport default {\n  name:'common-data-table',\n  components: {ElDataTable},\n  data: function() {\n    return {\n      form: [\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;title\&quot;,\n          label: \&quot;标题\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入标题\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: { placeholder: \&quot;请输入标题\&quot; }\n        },\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;tab\&quot;,\n          label: \&quot;类别\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入类别\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: { placeholder: \&quot;请输入类别\&quot; }\n        },\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;content\&quot;,\n          label: \&quot;内容\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入内容\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: {\n            placeholder: \&quot;请输入内容\&quot;,\n            type: \&quot;textarea\&quot;,\n            rows: 3\n          }\n        }\n      ],\n      columns: [\n        { prop: \&quot;id\&quot;, label: \&quot;ID\&quot; },\n        { prop: \&quot;title\&quot;, label: \&quot;标题\&quot; },\n        { prop: \&quot;tab\&quot;, label: \&quot;类别\&quot;, width: 100 }\n      ]\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <el-data-table url="https://cnodejs.org/api/v1/topics" data-path="data" :form-attrs="{ labelWidth: '80px' }" :custom-query="{ limit: 5 }" :pagination-sizes="[5,10,15]" :pagination-size="5" :columns="columns" :form="form">
      </el-data-table>
</template>


</div>
                            <p>通过<code>url</code>属性定义表单数据源，<code>data-path</code>属性定义对应数据源的字段，<code>columns</code>属性配置 table 的列，<code>form</code>属性新增和修改的属性 。</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">el-data-table</span>
      <span class="hljs-attr">url</span>=<span class="hljs-string">"https://cnodejs.org/api/v1/topics"</span>
      <span class="hljs-attr">data-path</span>=<span class="hljs-string">"data"</span>
      <span class="hljs-attr">:form-attrs</span>=<span class="hljs-string">"{ labelWidth: '80px' }"</span>
      <span class="hljs-attr">:custom-query</span>=<span class="hljs-string">"{ limit: 5 }"</span>
      <span class="hljs-attr">:pagination-sizes</span>=<span class="hljs-string">"[5,10,15]"</span>
      <span class="hljs-attr">:pagination-size</span>=<span class="hljs-string">'5'</span>
      <span class="hljs-attr">:columns</span>=<span class="hljs-string">"columns"</span>
      <span class="hljs-attr">:form</span>=<span class="hljs-string">"form"</span>
      &gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">el-data-table</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> ElDataTable <span class="hljs-keyword">from</span> <span class="hljs-string">'el-data-table'</span>

<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">name</span>:<span class="hljs-string">'common-data-table'</span>,
  <span class="hljs-attr">components</span>: {ElDataTable},
  <span class="hljs-attr">data</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>) </span>{
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">form</span>: [
        {
          <span class="hljs-attr">$type</span>: <span class="hljs-string">"input"</span>,
          <span class="hljs-attr">$id</span>: <span class="hljs-string">"title"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"标题"</span>,
          <span class="hljs-attr">rules</span>: [
            {
              <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>,
