              <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span>,
              <span class="hljs-attr">transform</span>: <span class="hljs-function"><span class="hljs-params">v</span> =&gt;</span> v &amp;&amp; v.trim()
            }
          ],
          <span class="hljs-attr">$el</span>: { <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"请输入标题"</span> }
        },
        {
          <span class="hljs-attr">$type</span>: <span class="hljs-string">"input"</span>,
          <span class="hljs-attr">$id</span>: <span class="hljs-string">"tab"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"类别"</span>,
          <span class="hljs-attr">rules</span>: [
            {
              <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>,
              <span class="hljs-attr">message</span>: <span class="hljs-string">"请输入类别"</span>,
              <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span>,
              <span class="hljs-attr">transform</span>: <span class="hljs-function"><span class="hljs-params">v</span> =&gt;</span> v &amp;&amp; v.trim()
            }
          ],
          <span class="hljs-attr">$el</span>: { <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"请输入类别"</span> }
        },
        {
          <span class="hljs-attr">$type</span>: <span class="hljs-string">"input"</span>,
          <span class="hljs-attr">$id</span>: <span class="hljs-string">"content"</span>,
          <span class="hljs-attr">label</span>: <span class="hljs-string">"内容"</span>,
          <span class="hljs-attr">rules</span>: [
            {
              <span class="hljs-attr">required</span>: <span class="hljs-literal">true</span>,
              <span class="hljs-attr">message</span>: <span class="hljs-string">"请输入内容"</span>,
              <span class="hljs-attr">trigger</span>: <span class="hljs-string">"blur"</span>,
              <span class="hljs-attr">transform</span>: <span class="hljs-function"><span class="hljs-params">v</span> =&gt;</span> v &amp;&amp; v.trim()
            }
          ],
          <span class="hljs-attr">$el</span>: {
            <span class="hljs-attr">placeholder</span>: <span class="hljs-string">"请输入内容"</span>,
            <span class="hljs-attr">type</span>: <span class="hljs-string">"textarea"</span>,
            <span class="hljs-attr">rows</span>: <span class="hljs-number">3</span>
          }
        }
      ],
      <span class="hljs-attr">columns</span>: [
        { <span class="hljs-attr">prop</span>: <span class="hljs-string">"id"</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">"ID"</span> },
        { <span class="hljs-attr">prop</span>: <span class="hljs-string">"title"</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">"标题"</span> },
        { <span class="hljs-attr">prop</span>: <span class="hljs-string">"tab"</span>, <span class="hljs-attr">label</span>: <span class="hljs-string">"类别"</span>, <span class="hljs-attr">width</span>: <span class="hljs-number">100</span> }
      ]
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="cha-xun-biao-dan"><a class="header-anchor" href="#cha-xun-biao-dan" aria-hidden="true">¶</a> 查询表单</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;el-data-table url=\&quot;https://cnodejs.org/api/v1/topics\&quot; data-path=\&quot;data\&quot; :form-attrs=\&quot;{ labelWidth: '80px' }\&quot; :custom-query=\&quot;{ limit: 5 }\&quot; :pagination-size=\&quot;5\&quot; :pagination-sizes=\&quot;[5,10,15]\&quot; :columns=\&quot;columns\&quot; :form=\&quot;form\&quot; :search-form=\&quot;searchForm\&quot;&gt;\n  &lt;/el-data-table&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nimport ElDataTable from 'el-data-table'\n\nexport default {\n  name:'common-data-table',\n  components: {ElDataTable},\n  data: function() {\n    return {\n      form: [\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;title\&quot;,\n          label: \&quot;标题\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入标题\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: { placeholder: \&quot;请输入标题\&quot; }\n        },\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;tab\&quot;,\n          label: \&quot;类别\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入类别\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: { placeholder: \&quot;请输入类别\&quot; }\n        },\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;content\&quot;,\n          label: \&quot;内容\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入内容\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: {\n            placeholder: \&quot;请输入内容\&quot;,\n            type: \&quot;textarea\&quot;,\n            rows: 3\n          }\n        }\n      ],\n      columns: [\n        { prop: \&quot;id\&quot;, label: \&quot;ID\&quot; },\n        { prop: \&quot;title\&quot;, label: \&quot;标题\&quot; },\n        { prop: \&quot;tab\&quot;, label: \&quot;类别\&quot;, width: 100 }\n      ],\n      searchForm:[\n        {\n          $type: \&quot;input\&quot;,\n          $id: \&quot;tab\&quot;,\n          label: \&quot;类别\&quot;,\n          rules: [\n            {\n              required: true,\n              message: \&quot;请输入类别\&quot;,\n              trigger: \&quot;blur\&quot;,\n              transform: v =&gt; v &amp;&amp; v.trim()\n            }\n          ],\n          $el: { placeholder: \&quot;请输入类别\&quot; }\n        }\n      ]\n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <el-data-table url="https://cnodejs.org/api/v1/topics" data-path="data" :form-attrs="{ labelWidth: '80px' }" :custom-query="{ limit: 5 }" :pagination-size="5" :pagination-sizes="[5,10,15]" :columns="columns" :form="form" :search-form="searchForm">
  </el-data-table>
</template>


</div>
                            <p>配置<code>searchForm</code>定义查询表单，通过 <code>slot=search</code>扩展查询表单，通过 <code>customQuery</code> 注入额外的查询参数 。</p>

