</div></demo-block>
<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<div class="tip">
<p><a href="https://femessage.github.io/el-data-table/">属性查看</a></p>
</div>
</section></template>

<script>
import ElDataTable from 'el-data-table'

export default {
  name:'common-data-table',
  components: {ElDataTable},
  data: function() {
    return {
      form: [
        {
          $type: "input",
          $id: "title",
          label: "标题",
          rules: [
            {
              required: true,
              message: "请输入标题",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "请输入标题" }
        },
        {
          $type: "input",
          $id: "tab",
          label: "类别",
          rules: [
            {
              required: true,
              message: "请输入类别",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "请输入类别" }
        },
        {
          $type: "input",
          $id: "content",
          label: "内容",
          rules: [
            {
              required: true,
              message: "请输入内容",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: {
            placeholder: "请输入内容",
            type: "textarea",
            rows: 3
          }
        }
      ],
      columns: [
        { prop: "id", label: "ID" },
        { prop: "title", label: "标题" },
        { prop: "tab", label: "类别", width: 100 }
      ],
      searchForm:[
        {
          $type: "input",
          $id: "tab",
          label: "类别",
          rules: [
            {
              required: true,
              message: "请输入类别",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "请输入类别" }
        }
      ]
    }
  }
}
</script>


