<template><section>
<h2 id="sidenav-zuo-ce-dao-hang-cai-dan"><a class="header-anchor" href="#sidenav-zuo-ce-dao-hang-cai-dan" aria-hidden="true">¶</a> SideNav 左侧导航菜单</h2>
<p>展厅左侧的导航栏菜单。</p>
<h3 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h3>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;side-nav :data=\&quot;navsData\&quot; :base=\&quot;`/component`\&quot;&gt;&lt;/side-nav&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        navsData: [\n          {\n            name: \&quot;Deepexi\&quot;,\n            href: \&quot;http://levy.ren/\&quot;\n          },\n          {\n            name: \&quot;使用指南\&quot;,\n            children: [\n              {\n                path: \&quot;/introduction\&quot;,\n                name: \&quot;介绍\&quot;\n              },\n              {\n                path: \&quot;/markdown\&quot;,\n                name: \&quot;Markdown\&quot;\n              },\n              {\n                path: \&quot;/side-nav\&quot;,\n                name: \&quot;导航菜单\&quot;\n              }\n            ]\n          },\n          {\n            name: \&quot;组件\&quot;,\n            groups: [\n              {\n                groupName: \&quot;Basic\&quot;,\n                children: [\n                  {\n                    path: \&quot;/button\&quot;,\n                    title: \&quot;Button 按钮\&quot;\n                  }\n                ]\n              }\n            ]\n          }\n        ]\n      };\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <side-nav :data="navsData" :base="`/component`"></side-nav>
</template>

</div>
                            <p>可配置一级菜单，次级菜单，次级菜单可以按组组织</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">side-nav</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"navsData"</span> <span class="hljs-attr">:base</span>=<span class="hljs-string">"`/component`"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">side-nav</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">navsData</span>: [
          {
            <span class="hljs-attr">name</span>: <span class="hljs-string">"Deepexi"</span>,
            <span class="hljs-attr">href</span>: <span class="hljs-string">"http://levy.ren/"</span>
          },
          {
            <span class="hljs-attr">name</span>: <span class="hljs-string">"使用指南"</span>,
            <span class="hljs-attr">children</span>: [
              {
                <span class="hljs-attr">path</span>: <span class="hljs-string">"/introduction"</span>,
                <span class="hljs-attr">name</span>: <span class="hljs-string">"介绍"</span>
              },
              {
                <span class="hljs-attr">path</span>: <span class="hljs-string">"/markdown"</span>,
                <span class="hljs-attr">name</span>: <span class="hljs-string">"Markdown"</span>
              },
              {
                <span class="hljs-attr">path</span>: <span class="hljs-string">"/side-nav"</span>,
                <span class="hljs-attr">name</span>: <span class="hljs-string">"导航菜单"</span>
              }
            ]
          },
          {
            <span class="hljs-attr">name</span>: <span class="hljs-string">"组件"</span>,
            <span class="hljs-attr">groups</span>: [
              {
                <span class="hljs-attr">groupName</span>: <span class="hljs-string">"Basic"</span>,
                <span class="hljs-attr">children</span>: [
                  {
                    <span class="hljs-attr">path</span>: <span class="hljs-string">"/button"</span>,
                    <span class="hljs-attr">title</span>: <span class="hljs-string">"Button 按钮"</span>
                  }
                ]
              }
            ]
          }
        ]
      };
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h3 id="attributes"><a class="header-anchor" href="#attributes" aria-hidden="true">¶</a> Attributes</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>base</td>
<td>路由前缀</td>
<td>string</td>
<td>medium / small / mini</td>
<td>—</td>
</tr>
<tr>
<td>data</td>
<td>菜单配置</td>
<td>array</td>
<td>-</td>
<td>—</td>
</tr>
</tbody>
</table>
<h4 id="cai-dan-xiang-shu-xing"><a class="header-anchor" href="#cai-dan-xiang-shu-xing" aria-hidden="true">¶</a> 菜单项属性</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>菜单名</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>href</td>
<td>外链地址</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>path</td>
<td>文档地址，匹配 docs 目录对应的文件</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>children</td>
<td>次级菜单列表</td>
<td>array</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>groups</td>
<td>次级菜单组</td>
<td>array</td>
<td>-</td>
<td>—</td>
</tr>
</tbody>
</table>
<h4 id="groups-zi-xiang-pei-zhi"><a class="header-anchor" href="#groups-zi-xiang-pei-zhi" aria-hidden="true">¶</a> groups 子项配置</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>groupName</td>
<td>组名</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>children</td>
<td>次级菜单列表</td>
<td>array</td>
<td>-</td>
<td>—</td>
</tr>
</tbody>
</table>
<h4 id="children-zi-xiang-pei-zhi"><a class="header-anchor" href="#children-zi-xiang-pei-zhi" aria-hidden="true">¶</a> children 子项配置</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>菜单名</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
<tr>
<td>path</td>
<td>文档地址，匹配 docs 目录对应的文件</td>
<td>string</td>
<td>-</td>
<td>—</td>
</tr>
</tbody>
</table>
</section></template>

<script>
  export default {
    data() {
      return {
        navsData: [
          {
            name: "Deepexi",
            href: "http://levy.ren/"
          },
          {
            name: "使用指南",
            children: [
              {
                path: "/introduction",
                name: "介绍"
              },
              {
                path: "/markdown",
                name: "Markdown"
              },
              {
                path: "/side-nav",
                name: "导航菜单"
              }
            ]
          },
          {
            name: "组件",
            groups: [
              {
                groupName: "Basic",
                children: [
                  {
                    path: "/button",
                    title: "Button 按钮"
                  }
                ]
              }
            ]
          }
        ]
      };
    }
  };
</script>


